import { Flex, Image, SimpleGrid, Stack, Text } from "@chakra-ui/react";
import React from "react";
import ContactsLink from "../components/ContactsLink";
import { IconSun, IconPhone, IconMapPin, IconAt } from "@tabler/icons";
export default function Contact({ contactRef }) {
  return (
    <SimpleGrid ref={contactRef} gap={40} alignItems={"center"} columns={[1, 1, 2, 2]} w={["80%", "80%", "80%", "50%"]} m="auto" mt={10}>
      <Image w={500} src={require("../images/Emails.png")} />
      <Flex flexDirection={"column"}>
        <Text className="fontLato" fontSize={48}>
          Contact
        </Text>
        <Flex w={"170px"}>
          <ContactsLink />
        </Flex>
        <Flex mt={10} justifyContent={"space-between"} w={"200px"} alignItems={"center"}>
          <IconAt color="#17A6C4" />
          <Flex flexDirection={"column"}>
            <Text fontSize={12} color={"gray"}>
              Email
            </Text>
            <Text>000done9@gmail.com</Text>
          </Flex>
        </Flex>
        <Flex mt={5} justifyContent={"space-between"} w={"150px"} alignItems={"center"}>
          <IconAt color="#17A6C4" />
          <Flex flexDirection={"column"}>
            <Text fontSize={12} color={"gray"}>
              Mobile
            </Text>
            <Text>+91 6261590850</Text>
          </Flex>
        </Flex>
        <Flex mt={3} justifyContent={"space-between"} w={"140px"} alignItems={"center"}>
          <IconSun color="#17A6C4" />
          <Flex flexDirection={"column"}>
            <Text fontSize={12} color={"gray"}>
              Working Hours
            </Text>
            <Text>9 a.m. - 5 p.m.</Text>
          </Flex>
        </Flex>
        <Flex mt={3} justifyContent={"space-between"} w={"100px"} alignItems={"center"}>
          <IconMapPin color="#17A6C4" />
          <Flex flexDirection={"column"}>
            <Text fontSize={12} color={"gray"}>
              Location
            </Text>
            <Text>Remote</Text>
          </Flex>
        </Flex>
      </Flex>
    </SimpleGrid>
  );
}
