import { createContext, useState } from "react";

export const ThemeContext = createContext();
export default function Theme({ children }) {
  const default_theme = localStorage.getItem("theme") ? localStorage.getItem("theme") : "light";
  if (!localStorage.getItem("theme")) {
    localStorage.setItem("theme", "light");
  }
  const [colorScheme, setColorScheme] = useState(default_theme);
  const toggleColorScheme = (value) => {
    setColorScheme(value || (colorScheme === "dark" ? "light" : "dark"));
    localStorage.setItem("theme", colorScheme === "dark" ? "light" : "dark");
  };
  return <ThemeContext.Provider value={{ colorScheme, toggleColorScheme }}>{children}</ThemeContext.Provider>;
}
