import { Flex } from "@chakra-ui/react";
import React from "react";
import { Tooltip } from "@mantine/core";
import { Icon } from "@iconify/react";
export default function ContactsLink() {
  const linkedIn = "https://www.linkedin.com/in/meshiv5/";
  const gmail = "mailto:000done9@gmail.com";
  const leetcode = "https://leetcode.com/Sh1vishere/";
  const github = "https://github.com/meshiv5";
  return (
    <Flex pt={15} justifyContent="space-between" w={"200px"} alignItems={"center"}>
      <Tooltip label="Linkedin" color="dark" withArrow>
        <Icon
          onClick={() => {
            window.open(linkedIn, "_blank", "noopener,noreferrer");
          }}
          className="icons"
          icon="entypo-social:linkedin-with-circle"
          color="#0E76A8"
          width={30}
        />
      </Tooltip>
      <Tooltip label="LeetCode" color="dark" withArrow>
        <Icon
          onClick={() => {
            window.open(leetcode, "_blank", "noopener,noreferrer");
          }}
          className="icons"
          icon="cib:leetcode"
          color="#0E76A8"
          width={30}
        />
      </Tooltip>
      <Tooltip label="Github" color="dark" withArrow>
        <Icon
          onClick={() => {
            window.open(github, "_blank", "noopener,noreferrer");
          }}
          className="icons"
          icon="codicon:github-inverted"
          color="blue"
          width={30}
        />
      </Tooltip>
      <Tooltip label="Email" color="dark" withArrow>
        <Icon
          onClick={() => {
            window.open(gmail, "_blank", "noopener,noreferrer");
          }}
          className="icons"
          icon="akar-icons:google-contained-fill"
          width={30}
          color="red"
        />
      </Tooltip>
    </Flex>
  );
}
