import React from "react";
import { Center, Flex, Image, SimpleGrid, Stack, Text, VStack } from "@chakra-ui/react";
import { Tooltip } from "@mantine/core";
import { Icon } from "@iconify/react";
export default function Education({ educationRef }) {
  return (
    <VStack ref={educationRef}>
      <SimpleGrid gap={40} alignItems={"center"} columns={[1, 1, 2, 2]} w={["80%", "80%", "80%", "50%"]} m="auto" mt={10} mb={10}>
        <Image w={500} src={require("../images/Education.png")} />
        <Flex flexDirection={"column"} alignItems={"center"}>
          <Text fontSize={48}>Education</Text>
          <Center w={"max-content"} fontSize={22}>
            Basic Qualification and Certifications
          </Center>
          <Flex alignItems={"center"} justifyContent={"space-between"} w={"80px"} mt={"10px"}>
            <Tooltip label="Leetcode" color="dark" withArrow>
              <Icon
                onClick={() => {
                  window.open("https://leetcode.com/Sh1vishere/", "_blank");
                }}
                className="icons"
                icon="simple-icons:leetcode"
                color="orange"
                width={25}
              />
            </Tooltip>
            <Tooltip label="Hackerrank" color="dark" withArrow>
              <Icon
                onClick={() => {
                  window.open("https://www.hackerrank.com/sh1vishere", "_blank");
                }}
                className="icons"
                icon="la:hackerrank"
                color="green"
                width={25}
              />
            </Tooltip>
          </Flex>
        </Flex>
      </SimpleGrid>
      {/* <Center fontSize={"34px"}>Degrees / Qualification</Center> */}
      {/* <SimpleGrid border={"1px solid red"} templateColumns={[1, 1, 2, 2]}>
        <Image src={require("../images/school.jpg")} />
        <SimpleGrid>
          <Text>Nascent Indian School</Text>
          <Text>12th Class</Text>
          <Text>Course : Maths</Text>
        </SimpleGrid>
      </SimpleGrid> */}
    </VStack>
  );
}
