import { Center, Flex, Image, SimpleGrid, Stack, Text } from "@chakra-ui/react";
import React from "react";
import ContactsLink from "../components/ContactsLink";
import { IconSun, IconPhone, IconMapPin, IconAt } from "@tabler/icons";
import GithubStats from "../components/GithubStats";
import { useEffect } from "react";
import GitHubCalendar from "github-calendar";
export default function Github({ githubRef }) {
  useEffect(() => {
    GitHubCalendar(".calendar", "meshiv5", { responsive: true });
  });
  return (
    <SimpleGrid ref={githubRef} gap={20} alignItems={"center"} w={["80%", "80%", "80%", "50%"]} m="auto" mt={10} pb={20}>
      <Center className="fontLato" fontSize={34}>
        Github Stats
      </Center>
      <GithubStats />
      <Center className="fontLato" fontSize={34}>
        Github Calendar
      </Center>
      <div className="calendar"></div>
    </SimpleGrid>
  );
}
